import { toast } from './toast';

const downloadBlob = (blob, fileName) => {
  const url = window.URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.target = '_blank';
  a.style.display = 'none';
  document.body.appendChild(a);
  a.download = fileName;
  a.click();
  window.URL.revokeObjectURL(url);
  document.body.removeChild(a);
};
const savingFile = (blob, fileName) => {
  const reader = new FileReader();
  reader.addEventListener('loadend', () => downloadBlob(blob, fileName));
  reader.readAsText(blob);
};
const resBlob = (reader, data, type) =>
  new Promise((resolve) => {
    function push() {
      reader.read().then(({ done, value }) => {
        data.push(value);
        done ? resolve(new Blob(data, { type })) : push();
      });
    }
    push();
  });
const readStream = (reader) =>
  new ReadableStream({
    start(controller) {
      return push();
      function push() {
        return reader.read().then((res) => {
          const { done, value } = res;
          if (done) {
            controller.close();
          }
          controller.enqueue(value);
          return push();
        });
      }
    },
  });
const qs = (params) => {
  let qstring = '';
  Object.keys(params).forEach((item, index) => {
    if (index === 0) {
      qstring += `?${item}=${params[item]}`;
    } else {
      qstring += `&${item}=${params[item]}`;
    }
  });
  return qstring;
};
