export const download = ({
  body = '',
  query = '',
  filename,
  ContentType = 'application/x-www-form-urlencoded',
  methods = 'GET',
  url,
}) => {
  if (!url) return;
  const option = {
    methods,
    credentials: 'include',
    headers: new Headers({
      'Content-Type': ContentType,
    }),
    body: JSON.stringify(body),
  };
  let requestUrl = url;
  if (query) {
    requestUrl += `${qs(query)}`;
  }
  return fetch(`${requestUrl}`, option)
    .then((res) => {
      if (!filename) {
        filename = decodeURIComponent(
          res.headers
            .get('content-disposition')
            .replace('attachment;filename=', '')
        );
      }
      return new Response(readStream(res.body.getReader()));
    })
    .then((res) =>
      resBlob(res.body.getReader(), [], res.headers.get('Content-Type'))
    )
    .then((response) => savingFile(response, filename))
    .catch((error) => toast(`${error.message}è¯·åæ¬¡å°è¯`));
};
